import Page from './index'
import Vue from 'vue'

export default {
    data() {
        return {
            Page
        }
    },
    methods: {
        pushPage(vueInstance) {
            this.$parent.pageStack.push(vueInstance)
        },
        toLanfing() {
            Vue.set(this.$parent, 'pageStack', [this.Page.Landing])
        }
    }
}



